.it {
  &__container {
    &--slider {
      position: relative;

      @include mobile {
        padding: 0;
      }
    }
  }

  &__header {
    align-items: center;
    justify-content: space-between;
    margin-bottom: 72px;

    @include mobile {
      margin-bottom: 48px;
    }
  }

  &__left {
    width: 50%;

    @include tablet {
      width: 100%;
    }
  }

  &__meta {
    margin-bottom: 48px;
  }

  &__title {
    margin-bottom: 26px;
  }

  &__btn {
    margin-top: 35px;

    @include tablet {
      display: none;
    }
  }

  &__cards {
    grid-template-rows: repeat(2, 1fr);
    grid-auto-rows: auto;
    gap: 22px;
    margin-bottom: 37px;

    @include mobile {
      align-items: start;
    }
  }
}
