.meta {
  display: inline-flex;
  gap: 8px;

  &__item {
    border-radius: 90px;
    padding: 5px 32px;
  }

  &__number {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 48px;
    color: var(--light-color);
    background-color: var(--meta);
  }

  &__icon {
    border: 2px solid var(--meta);
  }

  &__svg {
    width: 48px;
    height: 48px;
    fill: var(--meta);
  }
}
