.revenue {
  &__wide-container {
    position: relative;

    border: 1px solid var(--alto);
    border-radius: 32px;

    @include tablet {
      padding: 0;
    }
  }

  &__container {
    padding-top: 84px;
    padding-bottom: 122px;
    grid-template-rows: 130px 94px 1fr 1fr;

    @media (max-width: 1490px) {
      grid-template-rows: 1fr 94px 1fr 1fr;
    }

    @include tablet {
      display: flex;
      flex-direction: column;
      padding: 64px 32px;
    }

    @include mobile {
      padding: 64px 16px;
    }
  }

  &__image-wrapper {
    grid-column: 1 / span 5;
    grid-row: 1 / 4 span;

    @include tablet {
      margin-bottom: 60px;
      order: 2;
    }
  }

  &__image {
    position: absolute;
    left: 6px;
    display: block;

    @media (max-width: 1490px) {
      position: static;
    }
  }

  &__title {
    grid-column: 6 / span 6;
    padding-left: 50px;
    padding-top: 13px;

    @include tablet {
      margin-bottom: 26px;
      padding: 0;
      order: 0;
    }
  }

  &__descr {
    grid-column: 6 / span 6;
    grid-row: 2 / span 1;
    padding-left: 50px;
    padding-top: 10px;

    @include tablet {
      margin-bottom: 60px;
      padding: 0;
      order: 1;
    }
  }

  &__list {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, auto);
    align-items: start;
    column-gap: 0;
    row-gap: 59px;
    grid-column: 6 / span 7;
    grid-row: 3 / span 2;
    padding-left: 50px;

    @include tablet {
      row-gap: 85px;
      padding: 0;
      order: 3;
    }

    @include mobile {
      display: flex;
      flex-direction: column;
      gap: 42px;
    }
  }
}
