.contents {
  padding-top: 4px;

  &__container {
    align-items: start;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 64px;

    @include mobile {
      gap: 32px;
    }
  }

  &__left {
    width: 65%;

    @include tablet {
      width: 100%;
    }
  }

  &__title {
    margin-bottom: 26px;
  }

  &__btn {
    margin-top: 10px;

    @include tablet {
      order: 2;
    }

    @include mobile {
      width: 100%;
    }
  }

  &__list {
    gap: 22px;
    width: 100%;

    @include tablet {
      display: flex;
      flex-direction: column;
    }
  }
}
