.result-card {
  &__image {
    display: block;
    border-radius: 32px;
    margin-bottom: 25px;
  }

  &__adress {
    margin: 0;
    margin-bottom: 36px;
    padding: 0;
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 24px;
    line-height: 120%;
    color: var(--dark-grey);
  }

  &__info {
    width: 100%;
  }

  &__list {
    flex-direction: column;
    gap: 12px;
  }

  &__item {
    td {
      padding-bottom: 10px;
    }
  }

  &__value {
    font-weight: 500;
  }
}
