.slider-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 180px;
  width: 48px;
  height: 48px;
  color: var(--accent-color);
  box-shadow: 0 2px 24px 0 rgba(0, 0, 0, 0.2);
  background-color: var(--light-color);
  transition: background-color 0.3s ease-in-out;
  transform: translateY(-50%);
  cursor: pointer;

  &:hover {
    background-color: var(--light-grey);
  }

  @include big-desktop {
    right: 0;
  }

  @include mobile {
    width: 24px;
    height: 24px;
  }

  &__svg {
    width: 11px;
    height: 20px;
    fill: none;
    stroke: currentColor;
    stroke-width: 3px;

    @include mobile {
      width: 5px;
      height: 10px;
      stroke-width: 2px;
    }
  }
}
