.input {
  border: none;
  border-radius: 90px;
  padding: 24px 35px;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 24px;
  line-height: 120%;
  color: var(--dark-color);
  background-color: var(--light-color);

  @include mobile {
    padding: 12.5px 35px;
    font-size: 16px;
  }
}
