.pavilion {
  padding-bottom: 190px;

  @include tablet {
    padding-bottom: 120px;
  }

  &__container {
    position: relative;
    grid-template-rows: repeat(6, 1fr);

    @include tablet {
      display: flex;
      flex-direction: column;
      gap: 48px;
    }
  }

  &__image-wrapper {
    display: block;
    grid-column: 1 / span 5;
    grid-row: 1 / span 6;

    @include tablet {
      order: 1;
    }
  }

  &__image {
    position: absolute;
    display: block;

    @include big-desktop {
      position: static;
    }
  }

  &__meta {
    padding-left: 65px;
    padding-top: 26px;
    align-self: start;
    grid-column: 6 / span 7;
    margin-right: -20px;

    @include tablet {
      order: 0;
      padding: 0;
    }
  }

  &__content {
    padding-left: 65px;
    padding-top: 46px;
    grid-column: 6 / span 7;
    grid-row: 2 / span 5;

    @include tablet {
      order: 2;
      padding: 0;
      width: 87%;
    }
  }

  &__title {
    margin: 0;
    padding: 0;
    margin-bottom: 26px;
  }

  &__descr {
    margin: 0;
    padding: 0;
    margin-bottom: 26px;
  }

  &__info {
    margin: 0;
    padding: 0;
    margin-bottom: 72px;
    width: 85%;

    @include tablet {
      width: 100%;
    }
  }

  &__list {
    flex-direction: column;
    gap: 15px;
  }

  &__item {
    padding: 2px 0;
    padding-left: 40px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 20px;
    line-height: 120%;
    color: var(--dark-grey);
    background-image: url('../img/drop.svg');
    background-position: left center;
    background-size: 28px 28px;
    background-repeat: no-repeat;
  }
}
