.footer {
  margin-top: -195px;
  padding-top: 271px;
  padding-bottom: 87px;
  background-color: var(--mine-shaft);

  @include tablet {
    padding-top: 271px;
    padding-bottom: 24px;
  }

  &__container {
    flex-wrap: wrap;
    align-items: start;

    @include big-desktop {
      justify-content: space-between;
      row-gap: 20px;
    }

    @include tablet {
      display: grid;
      grid-template-rows: repeat(4, 1fr);
      grid-auto-rows: 1fr;
    }

    @include big-mobile {
      display: flex;
      flex-direction: column;
      gap: 85px;
    }
  }

  &__logo {
    display: block;
    margin-right: 135px;

    @include tablet {
      grid-column: 1 / span 4;
      margin-right: 0px;
      font-size: 18px;
    }

    @include big-mobile {
      order: 3;
    }
  }

  &__policies {
    flex-direction: column;
    gap: 17px;
    margin-right: 65px;
    padding-top: 17px;
    max-width: 388px;

    @include big-desktop {
      margin-right: 0;
    }

    @include tablet {
      grid-column: 5 / span 6;
      grid-row: 1 / span 4;
      padding-top: 0px;
    }
  }

  &__company-info {
    flex-direction: column;
    margin-right: auto;
    padding-top: 17px;
    max-width: 274px;

    @include big-desktop {
      margin-right: 0;
    }

    @include tablet {
      grid-column: 5 / span 6;
      grid-row: 4 / span 2;
      padding-top: 0px;
    }
  }

  &__text-item {
    margin: 0;
    padding: 0;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 18px;
    line-height: 120%;
    color: var(--silver-chalice);

    @include tablet {
      font-size: 16px;
    }
  }

  &__tel {
    padding-top: 17px;
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 18px;
    line-height: 120%;
    color: var(--light-color);

    svg {
      stroke: var(--anzac);
    }

    @include tablet {
      grid-column: 1 / span 6;
      grid-row: 2 / span 1;
      padding-top: 17px;
      font-size: 18px;
    }
  }
}
