.stats {
  &__value {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-bottom: 17px;

    &--small-offset {
      margin-bottom: 8px;
    }
  }

  &__number {
    margin-right: 16px;
    font-weight: 600;
    font-size: 86px;
    line-height: 100%;
    color: var(--accent-color);

    @include tablet {
      font-size: 64px;
    }

    @include mobile {
      font-size: 36px;
    }
  }

  &__label {
    font-weight: 500;
    font-size: 32px;
    line-height: 120%;
    color: var(--dark-color);

    @include tablet {
      font-size: 24px;
    }

    @include mobile {
      font-size: 18px;
    }
  }

  &__descr {
    margin: 0;
    padding: 0;
    font-size: 20px;

    @include tablet {
      font-size: 16px;
    }
  }
}
