.income {
  padding-top: 148px;
  padding-bottom: 154px;

  @include tablet {
    padding-top: 120px;
    padding-bottom: 184px;
  }

  @include mobile {
    padding-top: 76px;
    padding-bottom: 76px;
  }

  &__title {
    margin-bottom: 64px;
  }

  &__cards {
    display: flex;
    flex-wrap: wrap;
    column-gap: 24px;
    row-gap: 64px;

    @include tablet {
      column-gap: 20px;
      row-gap: 48px;
    }

    @include mobile {
      column-gap: 8px;
      row-gap: 20px;
    }
  }

  &__item {
    width: calc(((100% - (4 - 1) * 24px)) / 4);

    @include tablet {
      width: calc(((100% - (3 - 1) * 20px)) / 3);
    }

    @include small-tablet {
      width: calc(((100% - (2 - 1) * 20px)) / 2);
    }
  }

  &__image {
    display: block;
    border-radius: 32px;
    margin-bottom: 26px;

    @include mobile {
      border-radius: 12px;
      margin-bottom: 10px;
    }
  }

  &__descr {
    margin: 0;
    padding: 0;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 20px;
    line-height: 120%;
    color: var(--dark-grey);

    @include mobile {
      font-size: 14px;
    }
  }
}
