.about {
  padding-bottom: 182px;

  @include tablet {
    padding-bottom: 120px;
  }

  @include mobile {
    padding-bottom: 77px;
  }

  &__container {
    &--not-offset {
      @include tablet {
        padding: 0;
      }
    }

    &--slider {
      position: relative;
    }
  }

  &__title {
    margin-bottom: 67px;

    @include tablet {
      margin-bottom: 70px;
    }
  }

  &__list {
    flex-wrap: wrap;
    gap: 27px;
    margin-bottom: 69px;

    @include tablet {
      gap: 45px;
    }
  }

  &__item {
    padding-left: 33px;
    max-width: 452px;
    background-image: url('../img/star.svg');
    background-position: left top;
    background-size: 20px 20px;
    background-repeat: no-repeat;

    @include tablet {
      max-width: 100%;
      width: 100%;
    }
  }

  &__video {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 32px;
    margin-bottom: 70px;
    width: 100%;
    height: 567px;
    background-image: url('../img/about-video-image.jpg');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;

    @include tablet {
      margin-bottom: 59px;
      background-position: left center;
    }

    @include mobile {
      margin-bottom: 20px;
      border-radius: 13.33px;
      height: 236px;
    }
  }
}
