.tel {
  display: flex;
  align-items: center;
  font-weight: 600;
  font-size: 18px;
  line-height: 120%;
  color: var(--dark-color);

  &__svg {
    margin-right: 15px;
    width: 24px;
    height: 24px;
    fill: none;
    stroke-width: 2px;
  }

  @include tablet {
    font-size: 14px;
    background-size: 20px 20px;
  }
}
