.format-counter {
  counter-reset: my-awesome-counter;
  list-style: none;

  &__item {
    counter-increment: my-awesome-counter;
  }

  &__title {
    position: relative;
    padding-left: 52px;

    @include mobile {
      padding-left: 40px;
    }

    &::before {
      content: '0' counter(my-awesome-counter) '.';
      position: absolute;
      left: 0;
      top: 0;
      color: var(--raw-sienna);
    }
  }
}
