$options: (
  parentClass: 'format__list',
  itemsClass: 'format__item',
  desktopGap: 40px,
  desktopElems: 2,
  tablet: '1024px',
  tabletElems: 1,
  tabletGap: 42px,
  mobile: '768px',
  mobileElems: 1,
  mobileGap: 42px,
);

@include flex-layout($options);

.format {
  padding-top: 78px;
  padding-bottom: 83px;

  &__container {
    flex-wrap: wrap;

    &--list {
      @include mobile {
        padding: 0;
      }
    }
  }

  &__list {
    @include tablet {
      flex-direction: column;
    }

    @include mobile {
      gap: 20px;
    }
  }

  &__title {
    margin-bottom: 70px;

    @include tablet {
      margin-bottom: 56px;
    }
  }
}
