.result {
  padding-top: 147px;
  padding-bottom: 135px;

  @include tablet {
    padding: 120px 0;
  }

  @include mobile {
    padding: 76px 0;
  }

  &__container {
    &--slider {
      position: relative;
    }
  }

  &__title {
    padding-bottom: 88px;

    @include tablet {
      padding-bottom: 64px;
    }

    @include mobile {
      padding-bottom: 50px;
    }
  }
}
