.presentation {
  &__container {
    padding: 0;
    border-radius: 32px;
    background-color: var(--cardinal);
    background-image: url('../img/pres-bg-1.svg'), url('../img/books.png');
    background-position: left bottom, 112.2% 80%;
    background-size: auto, auto;
    background-repeat: no-repeat, no-repeat;

    @include big-desktop {
      background-position: center bottom, 120% bottom;
      background-repeat: no-repeat, no-repeat;
    }

    @include tablet {
      display: flex;
      flex-direction: column;
      background-image: url('../img/books.png'), url('../img/pres-bg-1.svg');
      background-size: 682px 600px, auto;
      background-position: center 101%, left bottom;
    }

    @include mobile {
      background-size: 295px 260px, auto;
      background-position: center bottom, left bottom;
    }
  }

  &__content {
    grid-column: 1 / span 9;
    padding-left: 107px;
    padding-top: 108px;
    padding-bottom: 117px;

    @include tablet {
      padding: 81px 31px;
      padding-bottom: 648px;
    }

    @include mobile {
      padding: 46px 16px;
      padding-bottom: 285px;
    }
  }

  &__title {
    margin-bottom: 46px;
    font-weight: 700;
    font-size: 86px;
    line-height: 100%;

    @media (max-width: 660px) {
      font-size: 36px;
    }
  }

  &__descr {
    margin-bottom: 72px;
    width: 85%;

    @include tablet {
      width: 100%;
    }

    @include mobile {
      margin-bottom: 48px;
    }
  }

  &__form {
    align-items: center;
    gap: 9px;

    @include tablet {
      justify-content: space-between;
    }

    @include mobile {
      flex-wrap: wrap;
    }
  }

  &__input {
    @include tablet {
      width: 100%;
    }

    @include mobile {
      &::placeholder {
        text-align: center;
      }
    }
  }

  &__button {
    @include mobile {
      width: 100%;
    }
  }
}
