.section-title {
  margin: 0;
  padding: 0;
  font-family: var(--font-family);
  font-weight: 600;
  font-size: 50px;
  line-height: 100%;
  color: var(--dark-color);

  @include tablet {
    font-size: 48px;
  }

  @include mobile {
    font-size: 32px;
  }

  &--light {
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 76px;
    line-height: 90%;
    color: var(--light-color);

    @include tablet {
      font-size: 48px;
    }

    @include mobile {
      font-size: 26px;
    }
  }
}
