.hero {
  &__container {
    border-radius: 32px;
    margin: 0 auto;
    padding: 0 107px;
    background-color: var(--accent-color);
    background-image: url('../img/hero-img.png');
    background-size: 904px 843px;
    background-position: right bottom;
    background-repeat: no-repeat;

    @include big-desktop {
      padding: 0 50px;
      background-size: 800px 746px;
    }

    @include tablet {
      background-size: auto;
      background-image: url('../img/hero-img-tablet.png');
    }

    @include small-tablet {
      background-size: contain;
    }

    @include mobile {
      padding: 0 16px;
    }
  }

  &__content {
    padding-top: 136px;
    padding-bottom: 92px;

    @include big-desktop {
      padding-top: 120px;
      padding-bottom: 90px;
    }

    @include tablet {
      display: flex;
      flex-direction: column;
      padding-top: 64px;
      padding-bottom: 62.5px;
    }

    @include mobile {
      position: relative;
      padding-top: 54px;
      padding-bottom: 355px;
    }
  }

  &__text {
    grid-column: 7 span;
    margin-bottom: 86px;

    @include tablet {
      margin-bottom: 478px;
      grid-column: 12 span;
    }

    @include mobile {
      margin-bottom: 42px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 33px;

    @include tablet {
      margin-bottom: 32px;
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: 84px;

    @include tablet {
      margin-bottom: 36px;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 8px;

    @include tablet {
      gap: 10.5px;
    }

    @include mobile {
      gap: 5px;
    }
  }

  &__item {
    padding-left: 40px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 120%;
    color: var(--light-color);
    background-image: url('../img/check-square.svg');
    background-position: left center;
    background-size: 24px 24px;
    background-repeat: no-repeat;

    @include tablet {
      font-size: 16px;
    }

    @include mobile {
      padding-top: 5px;
      padding-bottom: 5px;
      font-size: 14px;
    }
  }

  &__buttons {
    gap: 6%;
    grid-column: 12 span;

    @include tablet {
      flex-direction: column;
      gap: 16px;
    }
  }

  &__button {
    &--second {
      @include mobile {
        position: absolute;
        bottom: -30px;
      }
    }
  }
}
