.about-slider {
  width: 105%;

  @include mobile {
    width: 100%;
  }

  &-wrapper {
    overflow: hidden;
  }

  &__item {
    border-radius: 32px;
  }

  &__image {
    display: block;
    border-radius: 32px;

    @include tablet {
      border-radius: 24px;
    }

    @include mobile {
      border-radius: 10px;
      width: 100%;
    }
  }

  &__btn {
    position: absolute;
    right: -0.8%;
    top: 50%;
    z-index: 10;
  }
}
