.feedback {
  &__container {
    position: relative;
    padding: 0;
    border-radius: 32px;
    background-color: var(--cardinal);
    background-image: url('/img/map.png');
    background-position: 89% bottom;
    background-size: auto;
    background-repeat: no-repeat;

    @include big-desktop {
    }

    @include tablet {
      display: flex;
      flex-direction: column;
      background-position: center bottom;
      background-size: 670px 387px;
    }

    @include mobile {
      background-size: 252px 145px;
    }
  }

  &__content {
    grid-column: 1 / span 10;
    padding-left: 107px;
    padding-top: 85px;
    padding-bottom: 102px;

    @include tablet {
      padding: 81px 31px;
      padding-bottom: 457px;
    }

    @include mobile {
      padding: 46px 16px;
      padding-bottom: 180px;
    }
  }

  &__title {
    margin-bottom: 32px;
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 76px;
    line-height: 90%;

    @media (max-width: 660px) {
      font-size: 36px;
    }
  }

  &__descr {
    margin-bottom: 143px;
    width: 78%;

    @include tablet {
      width: 100%;
    }

    @include mobile {
      margin-bottom: 48px;
    }
  }

  &__form {
    align-items: center;
    gap: 9px;

    @include tablet {
      justify-content: space-between;
    }

    @include mobile {
      flex-wrap: wrap;
    }
  }

  &__input {
    @include tablet {
      width: 100%;
    }

    @include mobile {
      &::placeholder {
        text-align: center;
      }
    }
  }

  &__button {
    @include mobile {
      width: 100%;
    }
  }
}
