.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 90px;
  padding: 24px 42px;
  font-weight: 500;
  font-size: 24px;
  color: var(--dark-color);
  background-color: var(--yellow);

  @include mobile {
    padding: 12.5px 42px;
    font-size: 16px;
  }

  &__svg {
    margin-right: 16px;
    fill: none;
    stroke: currentColor;
    stroke-width: 2px;
    width: 24px;
    height: 24px;
  }

  &--red {
    color: var(--light-color);
    background-color: var(--red);
  }

  &--brown {
    color: var(--light-color);
    background-color: var(--brown);

    @include tablet {
      font-size: 16px;
    }
  }

  &:hover {
    opacity: 0.8;
    transition: opacity 0.3s ease-in-out;
  }
}
