.format-card-table {
  &__row {
    font-size: 20px;
    line-height: 120%;

    @include mobile {
      font-size: 14px;
    }
  }

  &__key {
    padding-bottom: 11px;
    padding-right: 35px;
    color: var(--boulder);
  }

  &__value {
    padding-bottom: 11px;
    font-weight: 500;
    color: var(--cardinal);
  }
}
