.it-card {
  padding: 42px 24px;
  border-radius: 32px;
  background-color: var(--card);

  &__title {
    margin: 0;
    margin-bottom: 16px;
    padding: 0;
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 20px;
    line-height: 120%;
    color: var(--dark-color);
  }

  &__descr {
    margin: 0;
    margin-bottom: 26px;
    padding: 0;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 120%;
    color: rgba(51, 51, 51, 0.6);
  }

  &__list {
    flex-direction: column;
    gap: 15px;
  }

  &__item {
    padding-left: 25px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 18px;
    line-height: 120%;
    color: var(--dark-color);
    background-image: url('../img/plus-square.svg');
    background-size: 16px 16px;
    background-position: left top;
    background-repeat: no-repeat;
  }

  &__info {
    margin: 0;
    margin-bottom: 24px;
    padding: 0;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 18px;
    line-height: 120%;
    color: var(--dark-color);
  }

  &--1 {
    grid-column: 1 / span 3;

    @include tablet {
      grid-column: 1 / span 6;
    }

    @include mobile {
      grid-column: 1 / span 12;
    }
  }

  &--2 {
    grid-column: 4 / span 3;

    @include tablet {
      grid-column: 7 / span 6;
    }

    @include mobile {
      grid-column: 1 / span 12;
    }
  }

  &--3 {
    grid-column: 7 / span 6;

    & > .it-card__info {
      width: 75%;
    }

    @include tablet {
      grid-column: 1 / 12 span;
      grid-row: 2 / 1 span;
    }

    @include mobile {
      grid-row: 3 / 1 span;

      & > .it-card__info {
        width: 100%;
      }
    }
  }

  &--4 {
    grid-column: 1 / 6 span;
    grid-row: 2 / 1 span;

    @include tablet {
      grid-column: 1 / 12 span;
      grid-row: 3 / 1 span;
    }

    @include mobile {
      grid-row: 4 / 1 span;
    }
  }

  &--5 {
    grid-column: 7 / span 6;

    & > .it-card__info:nth-child(3) {
      width: 70%;
    }

    @include tablet {
      grid-column: 1 / 12 span;
      grid-row: 4 / 1 span;
    }

    @include mobile {
      grid-row: 5 / 1 span;

      & > .it-card__info:nth-child(3) {
        width: 100%;
      }
    }
  }
}
