.goal {
  padding-top: 36px;

  &__container {
    padding: 0;
  }

  &__content {
    border-radius: 32px;
    padding: 132px 108px 106px;
    background-color: var(--light-grey);

    @include tablet {
      display: flex;
      flex-direction: column;
      gap: 64px;
      padding: 96px 32px 102px;
    }

    @include mobile {
      gap: 36px;
      padding: 67px 15px 68.5px;
    }
  }

  &__title {
    grid-column: 1 / span 6;
    margin: 0;
    padding: 0;

    @include tablet {
      width: 73%;
    }

    @include mobile {
      width: 100%;
    }
  }

  &__text {
    display: flex;
    flex-direction: column;
    gap: 32px;
    grid-column: 7 / span 6;
    padding-left: 80px;
    background-image: url('../img/alert.svg');
    background-position: 40px top;
    background-size: 24px 24px;
    background-repeat: no-repeat;

    @include tablet {
      padding-left: 40px;
      background-position: left top;
    }
  }

  &__info {
    margin: 0;
    padding: 0;
    width: 92%;
  }
}
