.profits {
  padding-top: 153px;
  padding-bottom: 173px;

  @include tablet {
    padding: 100px 0;
  }

  @include mobile {
    padding: 117px 0 79px;
  }

  &__list {
    justify-content: space-between;

    @include tablet {
      flex-wrap: wrap;
      row-gap: 68px;
    }

    @include mobile {
      row-gap: 42px;
    }
  }

  &__item {
    @include tablet {
      width: 50%;
    }

    @include mobile {
      width: 100%;
    }
  }

  &-item {
    &__top {
      align-items: center;
      flex-wrap: wrap;
      margin-bottom: 10px;
    }

    &__number {
      margin-right: 16px;
      font-weight: 600;
      font-size: 86px;
      color: var(--accent-color);

      @include tablet {
        font-size: 64px;
      }

      @include mobile {
        font-size: 36px;
      }
    }

    &__text {
      font-weight: 500;
      font-size: 32px;

      @include tablet {
        font-size: 24px;
      }

      @include mobile {
        font-size: 18px;
      }
    }

    &__descr {
      font-size: 20px;

      @include tablet {
        font-size: 16px;
      }
    }
  }
}
