.format-card {
  display: flex;
  flex-direction: column;
  padding: 32px 24px 50px;
  border: 1px solid var(--light-grey);
  border-radius: 32px;

  @include mobile {
    padding-bottom: 30px;
  }

  &__image {
    display: block;
    border-radius: 28px;
    margin-bottom: 36px;

    @include tablet {
      width: 100%;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 26px;
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 32px;
    line-height: 120%;

    @include mobile {
      font-size: 20px;
    }
  }

  &__info {
    flex-direction: column;
    gap: 24px;
    border-bottom: 1px solid var(--alto);
    margin-bottom: 32px;
    padding-bottom: 36px;
    min-height: 181px;
  }

  &__descr {
    margin: 0;
    padding: 0;
  }
}
