.header {
  padding-top: 14px;
  padding-bottom: 9px;

  &__container {
    display: flex;
    align-items: center;
    margin: 0 auto;
    padding: 0 var(--container-offset);
    max-width: var(--container-width);
  }

  &__logo {
    margin-right: 129px;

    @include tablet {
      margin-right: 30px;
    }
  }

  &__descr {
    font-size: 18px;

    @include tablet {
      font-size: 13px;
    }

    @include small-tablet {
      display: none;
    }
  }

  &__tel {
    display: flex;
    align-items: center;
    margin-left: auto;
    color: var(--dark-color);

    svg {
      stroke: var(--tall-poppy);
    }

    @include tablet {
      font-size: 14px;

      svg {
        width: 20px;
        height: 20px;
        stroke: var(--tall-poppy);
      }
    }
  }
}
