.contents-card {
  border-radius: 32px;
  padding: 42px;
  padding-bottom: 48px;
  background-color: var(--card);

  &--1 {
    grid-column: 1 / span 6;
  }

  &--2 {
    grid-column: 7 / span 6;
  }

  &--3 {
    grid-column: 1 / span 6;
    grid-row: 2 / span 1;
  }

  &--4 {
    grid-column: 7 / span 6;
    grid-row: 2 / span 1;
  }

  &--5 {
    grid-column: 1 / span 12;
    grid-row: 3 / span 1;
  }

  &__title {
    margin: 0;
    margin-bottom: 18px;
    padding: 2px 0;
    padding-left: 44px;
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 24px;
    line-height: 120%;
    color: var(--dark-grey);
    background-position: left center;
    background-size: 34px 34px;
    background-repeat: no-repeat;

    @include mobile {
      padding-top: 3px;
      padding-bottom: 3px;
      font-size: 20px;
      background-position: left top;
    }

    &--1 {
      background-image: url('/img/contents-1.svg');
    }

    &--2 {
      background-image: url('/img/contents-2.svg');
    }

    &--3 {
      background-image: url('/img/contents-3.svg');
    }

    &--4 {
      background-image: url('/img/contents-4.svg');
    }

    &--5 {
      background-image: url('/img/contents-5.svg');
    }
  }

  &__list {
    flex-direction: column;
    gap: 4px;
  }

  &__item {
    padding-left: 27px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 120%;
    color: var(--dark-grey);

    @include mobile {
      font-size: 16px;
    }

    &::before {
      content: '' counter(my-awesome-counter) '';
      position: absolute;
      display: flex;
      align-items: center;
      justify-content: center;
      left: 0;
      top: 0;
      border-radius: 90px;
      width: 20px;
      height: 20px;
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 16px;
      line-height: 120%;
      color: var(--light-color);
      background-color: var(--brown);
    }
  }
}
