// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: 'Rubik', sans-serif;
  --content-width: 1440px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  --content-wide-width: 1624px;
  --container-wide-width: calc(
    var(--content-wide-width) + (var(--container-offset) * 2)
  );

  --offset: 32px;

  // colors
  --dark-color: #333;
  --light-color: #fff;
  --accent-color: #bd3029;
  --yellow: #e4cc41;
  --red: #be3029;
  --brown: #b0451e;
  --meta: #d07233;
  --dark-grey: #333;
  --grey: #999;
  --light-grey: #f5f5f6;
  --card: #f5f5f6;
  --cardinal: #c82c25;
  --alto: #d9d9d9;
  --raw-sienna: #d6913b;
  --boulder: #777;
  --mine-shaft: #343434;
  --silver-chalice: #b0b0b0;
  --tall-poppy: #ba302a;
  --anzac: #e4cc41;
}
