.play-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  width: 142px;
  height: 142px;
  background-color: var(--accent-color);
  transition: opacity 0.3s ease-in-out;

  &:hover {
    opacity: 0.9;
  }

  &__svg {
    margin-left: 15px;
    width: 62px;
    height: 70px;
    fill: var(--light-color);
    stroke: var(--light-color);

    @include mobile {
      margin-left: 5px;
      width: 26px;
      height: 29px;
    }
  }

  @include mobile {
    width: 59px;
    height: 59px;
  }
}
